export default {
  name: 'category',
  data() {
    return {
      // category: []
    };
  },
  props: {
    category: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    toDetail(item) {
      this.$router.push({
        query: {
          ...item,
          id: item.categoryId
        },
        name: 'categoryDetail'
      })
    }
  }
};




