import HotSongList from './hotSongList.vue'
import Category from './category.vue'
export default {
  name: 'songSheet',
  data() {
    return {
      recommend: [],
      hotSongList: [],
      options: this.options,
      sin: 0,
      ein: 9
    }
  },
  created() {
    this.getCategoryData()
    this.getHotSongList()
  },
  activated() {
    this.$refs.scroll && this.$refs.scroll.refresh()
  },
  methods: {
    onPullingUp() {
      this.getHotSongList()
    },
    async getHotSongList() {
      var option = {
        sin: this.sin,
        ein: this.ein
      }
      if (this.lasttime) {
        option.lasttime = this.lasttime
      }

      var { code, data } = await this.__getJson(this.__SONG_SHEET_LIST, option)
      // (data)
      let list = data.list
      list.forEach(item => {
        // console.log(item)
        item.id = item.dissid
      })
      if (code == this.__QERR_OK) {
        this.hotSongList.push(...list)
        this.sin += 10
        this.ein += 10
      }
    },

    async getCategoryData() {
      var { code, data } = await this.__getJson(
        `/getCategoryTags`
      )
      // (data)
      if (code == 0) {
        this.category = data.categories
        this._formatCategory()
      }
    },
    async _formatCategory() {
      let num = 3
      this.category.forEach((item, index) => {
        // console.log(index)
        if (index == 0) return

        const arr = item.items.slice(0, num)
        this.recommend.push(...arr)
        this.recommend.sort((a, b) => {
          return a.categoryId + b.categoryId
        })
      })
    }
  },
  components: {
    HotSongList,
    Category
  }
}



