export default {
  name: 'hotSongList',
  data() {
    return {
      lasttime: ''
    }
  },
  props: {
    hotSongList: {
      type: Array,
      default: []
    }
  },
  computed: {},
  updated() {
    this.$nextTick(() => {
      this.$emit('updated')
    })
  },
  methods: {
    list(item) {
      return {
        name: 'songList',
        query: { ...item, pic: item.imgurl }
      }
    }
  }
}



